/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.imgwb-icon-circle {
	animation: dilate 10s;
	animation-iteration-count: infinite;
}
@keyframes dilate {
    0%, 6% {
        r: 0;
    }
    10%, 100% {
        r: 20;
    }
}

.wp-block-images-with-benefits-image-plus {
	container-type: inline-size;
	font-size:12px;
}

.wp-block .components-notice {
	margin: 0px;
}

.imgwb-main {
	display: flex;
	min-height: 400px;
}

.imgwb-main .components-tab-panel__tabs {
	border-right: 1px solid #eee;
}

.imgwb-main .components-tab-panel__tabs-item {
	width: 128px;
}

@container (max-width: 600px) {
	.imgwb-main {
		flex-wrap: wrap;
	}

	.imgwb-main .components-tab-panel__tabs {
		flex-direction: row;
		overflow: scroll;
	}

	.imgwb-main .components-tab-panel__tabs-item {
		width: auto;
		white-space: nowrap;
	}
}

.imgwb-main .components-tab-panel__tabs-item:hover {
	background-color: #eeeeee;
}

.imgwb-main .components-tab-panel__tabs-item:focus:not(:disabled) {
    box-shadow: inset var(--wp-admin-border-width-focus) 0 var(--wp-admin-theme-color);
}

.imgwb-main .components-tab-panel__tabs-item.active-tab {
	background-color: #dddddd;
    box-shadow: inset var(--wp-admin-border-width-focus) 0 var(--wp-admin-theme-color);
}

.imgwb-nav-counter {
	display: inline-block;
    vertical-align: top;
    box-sizing: border-box;
    margin: -1px 0 0 4px;
    padding: 0 5px;
    min-width: 18px;
    height: 18px;
    border-radius: 9px;
    background-color: #d63638;
    color: #fff;
    font-size: 11px;
    line-height: 1.6;
    text-align: center;
}

.imgwb-main .components-tab-panel__tab-content {
	flex-grow: 1;
	overflow: auto;
	margin: 8px;
	padding: 4px;
}

.imgwb-variant-settings {
	margin-top: 12px;
}

.imgwb-variant-settings .components-tab-panel__tabs {
	border-right: none;
	border-bottom: 1px solid #eee;
	margin-bottom: 12px;
}

.imgwb-variant-settings .components-tab-panel__tabs-item:focus:not(:disabled) {
    box-shadow: inset 0 var(--wp-admin-border-width-focus) var(--wp-admin-theme-color);
}

.imgwb-variant-settings .components-tab-panel__tabs-item.active-tab {
    box-shadow: inset 0 var(--wp-admin-border-width-focus) var(--wp-admin-theme-color);
}

.imgwb-variant-strip {
	padding: 8px 0px 6px 0px;
	display: flex;
	overflow-x: auto;
}

.imgwb-preview-header {
	display: flex;
	align-items: center;
}

.imgwb-thumb {
	margin: 6px;
	min-width: 180px;
	max-width: 180px;
	display: flex;
	flex-direction: column;
	cursor: pointer;
	padding: 4px;
	box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
	transition: 400ms;
}
.imgwb-thumb:hover {
	background-color: #eeeeee;
	transform:scale(1.1);
}

.imgwb-thumb.imgwb-variant-editing {
	background-color: #dddddd;
    box-shadow: inset 0 var(--wp-admin-border-width-focus) var(--wp-admin-theme-color);
}

.imgwb-variant-label, .imgwb-thumb .components-dropdown {
	display: inline-block;
}

.imgwb-thumb .imgwb-variant-label {
	flex-grow: 1;
}

.imgwb-thumb .imgwb-variant-label input {
	margin-top: 2px;
	background: transparent;
	border: none;
}

.imgbw-preview-footer {
	flex-grow: 1;
	display: flex;
	min-height: 32px;
    margin: 0px 12px;
	align-items: flex-end;
	justify-content: space-between;
	font-size: 10px;
	color: #aaa;
}
.imgbw-preview-footer .dashicons {
	font-size: 16px;
}

.imgwb-notification-count {
	background-color: #FF0000;
	color: #eee;
	border-radius: 50%;
}

.imgwb-main .block-editor-media-placeholder {
	margin-top: 12px;
}

.imgwb-variant-settings-focus {
	width: 300px;
}

.imgwb-benefit-message {
	margin-bottom: 12px;
	background-color: #d6363820;
	color: #d63638;
}

.imgwb-benefit-message:before {
	content: 'BENEFIT';
	background-color: #d63638;
	color: #fff;
	padding: 4px;
	font-size: 10px;
	font-weight: 500;
	margin-right: 8px;
	margin-bottom: 12px;
}

.imgwb-sparkle-toggle {
	margin-top: 8px;
}

table.imgwb-captions-tbl {
	border-collapse: collapse;
}

.imgwb-caption-text {
	padding-top: 16px;
}

table.imgwb-captions-tbl .imgwb-caption:hover {
	background-color: #eeeeee;
}

.imgwb-caption-editing {
	background-color: #dddddd;
	box-shadow: inset var(--wp-admin-border-width-focus) 0 var(--wp-admin-theme-color);
}

.imgwb-inspector-heading {
	font-weight: 500;
}

.imgwb-caption-position {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: space-between;
}

.imgwb-caption-slider-y {
	transform: rotate(90deg);
	position: relative;
	width: 60px;
    left: 0px;
    top: 12px;
}
.imgwb-caption-slider-x {
	position: relative;
	width: 60px;
    left: -12px;
    top: 63px;
}

#font-picker {
	margin-bottom: 16px;
}

.imgwb-suggestion-nav {
	display:flex;
	justify-content: space-between;
	gap: 24px;
}

.imgwb-suggestion-preview {
	width: 240px;
	margin: 20px auto;
}

.imgwb-suggestion-accept, .imgwb-suggestion-reject {
	margin-top: 12px !important;
	width: 100%;
	display: block;
	text-align: center !important;
}

.imgwb-inspector-help {
    font-size: 10px;
    color: rgb(117, 117, 117);
}

.wp-block-images-with-benefits-image-plus .components-notice.is-dismissible {
    padding-right: 0px;
}

.imgwb-media-prompt {
    background-color: #eeffff;
    padding: 8px;
}

.imgwb-media-create-container {
	margin-left:12px;
}

.imgwb-thumb {
	position: relative;
}
.imgwb-winning-ribbon {
	position: absolute;
	top: -3px;
	left: -3.5px;
}
.imgwb-winning-ctvr {
	transform: rotate(-45deg);
	position: absolute;
	top: 10px;
	left: 6px;
	font-size: 10px;
	font-weight: 600;
	color: #eee;
}
.imgwb-winning-ctvr + .imgwb-preview-header {
	margin-left: 24px;
}
.imgwb-preview-header .components-dropdown {
	margin-top: -5px;
}

.wp-core-ui .attachment .thumbnail .centered svg {
    transform: translate(-50%,-50%);
}

.imgwb-status-pie {
	transform: rotate(-90deg);
	vertical-align: top;
	width: 20px;
	height: 20px;
}
.imgwb-status-stale {
	animation: imgwb-strobe 0.5s alternate infinite;
}
@keyframes imgwb-strobe {
    from { opacity: 0.2; }
    to { opacity: 0.8; }
}

.wp-block-images-with-benefits-image-plus .dashicons-format-image {
	margin-right: 15px;
}
